}
optgroup {
  font-weight: bold;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}
body {
  transition: 0.3s;
  position: relative;
  background-color: #f2f2f2;
}
body.no-scroll {
  overflow: hidden;
}
body.opacity-layer {
  position: relative;
  transition: 0.3s;
}
body.opacity-layer:after {
  content: '';
  opacity: 0.3;
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 2;
  transition: 0.3s;
}
@-ms-viewport {
