/*=====================
  Базовая типография
=====================*/
@font-face {
  font-family: 'futura-futuris';
  src: url('../fonts/FuturaFuturis/FuturaFuturisC.woff?eo4p0s#iefix') format('woff');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'futura-futuris';
  src: url('../fonts/FuturaFuturis/FuturaFuturisLightC.woff?eo4p0s#iefix') format('woff');
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: 'futura-futuris';
  src: url('../fonts/FuturaFuturis/FuturaFuturisC-Bold.woff?eo4p0s#iefix') format('woff');
  font-weight: 700;
  font-style: normal;
}
body {
  font-family: 'futura-futuris', sans-serif;
  font-size: 16px;
  line-height: 1;
  font-weight: 400;
  color: #0b0b0b;
}
a {
  color: inherit;
  text-decoration: none;
}
img {
  max-width: 100%;
}
p,
li {
  line-height: 1.2;
}
p:last-child,
li:last-child {
  margin-bottom: 0;
}
.header {
  padding: 10px 0 0;
}
.header__wrap {
  display: flex;
  align-items: center;
  position: relative;
}
.header .icons {
  margin-left: auto;
  display: flex;
  align-items: center;
  margin-right: 20px;
}
.header .icons__item {
  margin-left: 10px;
}
.header .icons svg {
  fill: #0b0b0b;
}
.header .call {
  display: flex;
  align-items: center;
}
.header .call__items {
  display: flex;
  flex-direction: column;
  margin-right: 10px;
}
.header .call__items a {
  font-size: 12px;
}
.header .call__img {
  width: 40px;
  min-width: 40px;
  height: 40px;
  border: 2px solid #0b0b0b;
  border-radius: 100px;
  overflow: hidden;
}
.header .call__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.footer {
  padding: 20px 0;
}
.footer__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer .social__item svg {
  fill: #0b0b0b;
}
.footer .link {
  margin: 15px 0;
}
.footer .company {
  margin: 0;
}
.modal {
  display: none;
  padding: 40px 15px;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  overflow: auto;
}
.modal__wrap {
  max-width: 576px;
}
.modal__content {
  background-color: #fff;
  padding: 60px 20px 20px;
  position: relative;
}
.modal__content .close {
  position: absolute;
  width: 25px;
  height: 25px;
  top: 20px;
  right: 20px;
}
.modal__content .close span {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 100%;
  height: 2px;
  background-color: #0b0b0b;
}
.modal__content .close span:nth-of-type(1) {
  transform: rotate(45deg);
}
.modal__content .close span:nth-of-type(2) {
  transform: rotate(-45deg);
}
.modal .form__title {
  color: #0b0b0b;
  font-size: 30px;
  font-weight: 700;
  margin: 0 0 10px;
  text-align: center;
}
.modal .form__title.one {
  color: #00c78a;
}
.modal .form__title.two {
  color: #005e98;
}
.modal .form__subtitle {
  margin: 0 0 20px;
  font-size: 20px;
  font-weight: 400;
  text-align: center;
  color: #0b0b0b;
}
.modal .form__item {
  width: 100%;
}
.modal .form__item:not(:last-of-type) {
  margin-bottom: 20px;
}
.modal .form__name {
  font-size: 16px;
  font-weight: 300;
  margin: 0 0 5px;
}
.modal .form__input {
  width: 100%;
  height: 40px;
  border: 1px solid #0b0b0b;
  font-size: 16px;
  font-weight: 300;
  padding: 0 15px;
  font-family: 'futura-futuris', sans-serif;
  outline: none;
}
.modal .form__submit {
  font-size: 20px;
  text-transform: uppercase;
  margin: 40px 0 0;
  border: 1px solid #0b0b0b;
  background: none;
  padding: 10px 20px;
  display: block;
  width: 100%;
  text-align: center;
}
.modal .form__submit.one {
  background-color: #00c78a;
  border-color: #00c78a;
  color: #fff;
}
.modal .form__submit.two {
  background-color: #005e98;
  border-color: #005e98;
  color: #fff;
}
.modal .form__agree {
  font-size: 12px;
  font-weight: 300;
  margin: 10px 0 0;
}
.modal .form__agree a {
  text-decoration: underline;
}
.main {
  padding: 25px 0;
}
.main .title {
  text-align: center;
  font-weight: 700;
  font-size: 35px;
  line-height: 1.2;
}
.main .title span {
  display: block;
}
.main .subtitle {
  margin: 15px 0 0;
  font-weight: 300;
  text-align: center;
  font-size: 20px;
}
.main .grid {
  display: flex;
  flex-direction: column;
  margin-top: 25px;
}
.main .grid__item {
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.main .grid__item:not(:last-of-type) {
  margin-bottom: 25px;
}
.main .grid__item_one {
  background-color: #00c78a;
  box-shadow: 0 15px 15px 1px rgba(0, 199, 138, 0.18);
  background-image: url("../img/house.png");
  background-position: right -20px bottom;
  background-repeat: no-repeat;
  background-size: 296px auto;
}
.main .grid__item_two {
  background-color: #005e98;
  box-shadow: 0 15px 15px 1px rgba(0, 94, 152, 0.18);
  background-image: url("../img/rub.png");
  background-position: right -9px bottom;
  background-repeat: no-repeat;
  background-size: 320px auto;
}
.main .grid__item_three {
  background-color: #ff966f;
  box-shadow: 0 15px 15px 1px rgba(255, 150, 111, 0.18);
  background-image: url("../img/iphone.png"), url("../img/iphone.png");
  background-position: right -38px bottom, right 50px bottom;
  background-repeat: no-repeat;
  background-size: 150px auto, 120px auto;
}
.main .grid__item .name {
  color: #fff;
  font-weight: 700;
  margin: 0 0 10px;
  font-size: 25px;
}
.main .grid__item .text {
  margin-bottom: 10px;
  max-width: 180px;
}
.main .grid__item .text p {
  color: #fff;
  margin: 0;
  font-size: 16px;
}
.main .grid__item .btn {
  background-color: #fff;
  color: #0b0b0b;
  font-size: 16px;
  padding: 10px;
  display: inline-block;
  margin-top: auto;
}
